import analytics from "./lib/analytics";
import trackers from "./lib/trackers";

const enableAnalytics = '[data-enable-analytics]';

const autoTracking = [
  trackers.externalClicks
    .attachSelector('a')
    .skipSelector('[href^="mailto:"]')
    .skipSelector('[href^="tel:"]')
    .skipSelector('[href*="api.nnip.com"]'),
  trackers.pdf
    .attachSelector('a[href*="/file?uuid"]')
    .attachSelector('a[href$=".pdf"]')
    .attachSelector('a[href*="api.nnip.com/DocumentsApi/files"]'),
  trackers.mailto
    .attachSelector('a[href^="mailto:"]'),
  trackers.tel
    .attachSelector('a[href^="tel:"]'),
];

const elementTracking = [
  trackers.accordeon
    .attachSelector('.c-expander-list [data-role="toggle"]')
    .attachSelector('.tab-button > a'),
  trackers.custom.accordeonInverse
    .attachSelector('.footer-link-list-group h5')
    .attachSelector('[data-component=Collapser]'),
  trackers.breadcrumb
    .attachSelector('.breadcrumb li a'),
  trackers.custom.breadcrumbProps
    .attachSelector('.breadcrumb li'),
  trackers.button
    .attachSelector('.button')
    .attachSelector('.c-button')
    .attachSelector('input[type="button"]')
    .attachSelector('input[type="submit"]'),
  trackers.checkbox
    .attachSelector(`${enableAnalytics}.c-form-checkbox:not(.c-form-checkbox--switch)`),
  trackers.contactList
    .attachSelector('.contact-list a')
    .attachSelector('.c-contact__contact-list a'),
  trackers.dropDown
    .attachSelector(`${enableAnalytics}.c-form-select`),
  trackers.toggle
    .attachSelector(`${enableAnalytics}.c-form-checkbox--switch`),
  trackers.lightbox
    .attachSelector('.c-modal__dialog')
    .attachSelector('.c-sidepanel'),
  trackers.linkDateList
    .attachSelector('.list--dates a'),
  trackers.linkList
    .attachSelector('.link-list a')
    .attachSelector('.c-list a')
    .attachSelector('.c-list-icon a')
    .attachSelector('a.c-link'),
  trackers.moreLinks
    .attachSelector('.more:not([href*="/file?uuid"])'),
  trackers.nba
    .attachSelector('[data-nba-code]'),
  trackers.radioButton
    .attachSelector(`${enableAnalytics}.c-form-radio`),
  trackers.socialShare
    .attachSelector('.icons--social-v2 a'),
  trackers.tab
    .attachSelector('.tabbed-component .tab-header a')
    .attachSelector('[role=tablist] label'),
  trackers.textLink
    .attachSelector('a.normal')
    .attachSelector('p a:not([class])')
    .skipSelector('[href^="mailto:"]')
    .skipSelector('[href^="tel:"]')
    .skipSelector('.icons--trust-builder a')
    .skipSelector('.icons--social-v2 a')
    .skipSelector('.link-list a')
    .skipSelector('.c-list a')
];

const navigation = [
  trackers.footerBottom
    .attachSelector('.footer-section .link-list-group--inline a'),
  trackers.footerIcons
    .attachSelector('.footer-section .icons--trust-builder a'),
  trackers.footerLinks
    .attachSelector('.footer-section .footer-link-list-group .link-list a'),
  trackers.hamburgerIcon
    .attachSelector('.c-header__buttons a.c-header__mobile-button'),
  trackers.loginIcon
    .attachSelector('.c-header__login > a')
    .attachSelector('.c-header__buttons a.c-header__user-button')
    .attachSelector('.c-header__mobile-user a.c-header__login-link'),
  trackers.logo
    .attachSelector('.c-main-navigation__logo')
    .attachSelector('.c-header__logo-container'),
  trackers.mainMenu
    .attachSelector('.c-main-navigation__main a:not(.c-button)')
    .attachSelector('.c-header__menu-item > a')
    .attachSelector('.c-header__mobile a.c-header__mobile-menu-link'),
  trackers.mainMenuDropdowns
    .attachSelector('.c-main-navigation__main [data-toggle="submenu"]'),
  trackers.searchIcon
    .attachSelector('.c-main-navigation__search .c-icon--icon-search')
    .attachSelector('.text-field--search-submit')
    .attachSelector('.c-header__search__form .c-search__submit'),
  trackers.secondaryNavigation
    .attachSelector('.c-header-dropdown a'),
  trackers.topItems
    .attachSelector('.c-main-navigation__portal-menu a')
    .attachSelector('.c-main-navigation__portal__dropdown a')
    .attachSelector('.c-header__switch-item > a'),
  trackers.topItemDropdowns
    .attachSelector('.c-main-navigation__portal [data-toggle="submenu"]'),
];

const videoTracking = [
  trackers.youtube
    .attachSelector('iframe[src*=youtube]'),
];

const virtualPage = [
  trackers.pageTracking
];

const errorTracking = [
  trackers.error
];

export function registerTrackers() {
  [
    ...autoTracking,
    ...elementTracking,
    ...navigation,
    ...videoTracking,
    ...virtualPage,
    ...errorTracking
  ].forEach(tracker => analytics.registerTracker(tracker));
}