import cms from './cms';
import meta from './meta';
import relay42 from './relay42';
import chatbot from './chatbot';
import analytics from './analytics';

export default function loadHeadScripts() {
  if (!cms.isEdit && !meta.PSDL_SKIP_HEADSCRIPTS) {
    relay42.load();
    analytics.load();
    chatbot.load();
  }
}

loadHeadScripts();
