import Tracker from "../../Tracker";
import { AUTO_TRACKING } from "../trackLabels";

export const externalClicks = new Tracker(AUTO_TRACKING, 'external-clicks', { addEventListener: false });

externalClicks.onElementAttached(element => {
  if (element.href && (element.hostname !== window.location.hostname)) {
    element.addEventListener('click', (event) => {
      externalClicks.publish({
        'event_action': 'click',
        'event_label': event.target.textContent
      });
    });
  }
});
