import meta from './meta.js';
import cookies from './cookies.js';
import { parseQueryString } from "@nn/psdl-utility/src/query-string.js";

const tagProperties = {
  _loaded: false,
  _data: {
    // Defaults
    platform_brand: 'NN',
    has_transacted: false,
    virtualpage: false,

    // Used in multiple Relay42 tags, not sure if tolerant for undefined values
    user_id_sap: null, // meta uis
    campaign_name: null, // meta campagnenaam
    user_cookie_consent: null, // cookie
  },
  get data() {
    this.load();
    return this._data;
  },
  add(newData) {
    this._data = {
      ...this.data,
      ...newData
    }
  },
  reload() {
    this._loaded = false;
    meta._loaded = false;
    cookies._loaded = false;
    this.load();
  },
  load() {
    if (this._loaded) {
      return;
    }
    this._loaded = true;
    this.add(meta.props);
    this.add(cookies.props);
    this.add(parseQueryString(window.location.search));

    const titleElement = document.querySelector('title');
    if (titleElement) {
      this.add({
        page_meta_title: titleElement.textContent,
        page_title_length: titleElement.textContent.length
      });
    }
  },
  transacted() {
    cookies.transacted();
  }
};

export default tagProperties;