import Tracker from '../../Tracker.js';
import { NAVIGATION } from "../trackLabels";

export const topItems = new Tracker(NAVIGATION, 'top-items');

export const topItemDropdowns = new Tracker(NAVIGATION, 'top-items', {
  data(element, event) {
    let state = 'close';
    let event_label = event.target.textContent;
    if (element.parentNode.classList.contains('c-main-navigation__expanded')) {
      state = 'open';
    }
    return {
      event_action: state,
      event_label: event_label
    }
  }
}
);