import meta from "@nn/psdl-utility/src/meta.helper";

const envMapping = {
  'DEV': 'dev',
  'TESTING': 'test',
  'UAT': 'test',
  'PRODUCTION': 'prod',
  'DEVELOPMENT': 'test'
};

const metaProperties = {
  campaign_name: 'campagnenaam',
  nn_doelgroep: 'targetgroup',
  nn_rdesign: 'devicetype',
  nn_segment: 'nn_branche',

  page_audience: 'doelgroep',
  page_category: 'categorie',
  page_type: 'type',
  page_id: 'page_id',
  page_meta_description: 'description',
  page_product: 'product',
  page_publish_date: 'page_publish_date',
  page_update_date: 'page_update_date',

  user_id_sap: 'uis',
};


const metaTags = [
  'portal',
  'env',
  'pagestructure',
];

export default {
  _loaded: false,
  _tags: {},
  _props: {},
  get tags() {
    this.readAll();
    return this._tags;
  },
  get props() {
    this.readAll();
    return this._props;
  },
  readAll() {
    if(this._loaded) {
      return;
    }
    this._loaded = true;
    Object.keys(metaProperties).forEach(key => {
      const content = meta.getMetaContentByName(metaProperties[key]);
      if (content) {
        this._props[key] = content;
      }
    });

    metaTags.forEach(tag => {
      this._tags[tag] = meta.getMetaContentByName(tag)
    });

    this.setDefaults();
    this.handleSpecialCases();
  },
  setDefaults() {
    this._tags.env = this._tags.env || 'DEV';
    this._tags.portal = this._tags.portal || 'nn.nl';
    this._props.platform_environment = envMapping[this._tags.env];
  },
  handleSpecialCases() {
    if (this._props.nn_segments) {
      this._props.nn_segments = this._props.nn_segments.replace(/^deelmijnietin$/, 'deel+mij+niet+in');
    }
  }
}
