import analytics from "../../analytics";
import documentState from "@nn/psdl-utility/src/documentState";

export const pageTracking = {
  scan() {
    if (this.initialized) {
      return;
    }
    this.init();
    this.initialized = true;
  },

  init() {
    analytics.onDataChange((data, propStatus) => {
      const pageTracking = data['page-tracking'];
      if (!pageTracking || !pageTracking.eec_data || !pageTracking.eec_data.action_data) {
        return;
      }

      const stepName = pageTracking.eec_data.action_data.eec_checkout_step_name;
      if (stepName !== this.currentStepName) {
        propStatus.stop = true;
        documentState.onWindowLoaded(() => {
          analytics.addLocalTagProperties(data);
          analytics.loadVirtualPage(stepName);
        })
      }
      this.currentStepName = stepName;
    });
  }
};