import analytics from './lib/analytics.js';
import radio from "radio";
import {registerTrackers} from './trackers.js';

function notLoaded () {
  throw new Error("Analytics is not being loaded for this page. Are PSDL head scripts disabled?")
}

window.app = window.app || {};
window.app.analytics = {
  getData: notLoaded,
  publish: notLoaded,
  prop: notLoaded,
  setConsoleLogs: notLoaded,
  transacted: notLoaded,
};

export default {
  load() {
    window.app.analytics = analytics;
    analytics.load();
    registerTrackers();
  },
};

// expose radio as global to ensure there is only 1 radio instance loaded at all times
global.radio = radio;
