import Tracker from "../../Tracker";
import { ELEMENT_TRACKING } from "../trackLabels";

export const accordeon = new Tracker(ELEMENT_TRACKING, 'accordeon',{
  action(element) {
    let state = 'open';
    if (element.parentNode.classList.contains('is-expanded') || element.parentNode.classList.contains('active')) {
      state = 'close';
    }
    return state;
  },
  label(element, event) {
    return event.target.textContent
  }
});

export const accordeonInverse = new Tracker(ELEMENT_TRACKING, 'accordeon',{
  action(element) {
    let state = 'open';
    if (!element.parentNode.classList.contains('active')) {
      state = 'close';
    }
    return state;
  },
  label(element, event) {
    return event.target.textContent
  }
});
