import Tracker from "../../Tracker";
import { ELEMENT_TRACKING } from "../trackLabels";

const socialTypes = ['facebook', 'twitter', 'linkedin'];

export const socialShare = new Tracker(ELEMENT_TRACKING, 'social-share', {
  label(element) {
    var socialType = '';
    socialTypes.forEach((type) => {
      if (element.href.indexOf(type) !== -1) {
        socialType = type;
      }
    });
    return socialType;
  }
});