import { forEach } from '@nn/psdl-utility/src/object-utils';
import cookie from '@nn/psdl-utility/src/cookie';
import UUID from '@nn/psdl-utility/src/uuid';

const DAYS_IN_YEAR = 365;
const THIRTY_MINUTES_AS_DAYS = 30 / (24 * 60);

const cookieProperties = {
  user_id_liveperson: 'LPVID',
  session_id_liveperson: 'LPSID',
  session_id_bc: 'BCSessionID',
  user_cookie_consent: 'BCPermissionLevel',
  user_id_optimizely: 'optimizelyEndUserId',
  browser_id: 'browser_id',
  session_id: 'session_id',
  has_transacted: 'has_transacted'
};

const cookies = {
  _loaded: false,
  _props: {},
  get props() {
    this.readAll();
    return this._props;
  },
  readAll() {
    if(this._loaded) {
      return;
    }
    this._loaded = true;

    forEach(cookieProperties, (value, key) => {
      const content = cookie.get(value);
      if (content) {
        this._props[key] = content;
      }
    });

    if (!this._props.browser_id) {
      this._props.browser_id = UUID();
      cookie.set('browser_id', this._props.browser_id, DAYS_IN_YEAR);
    }

    if (!this._props.session_id) {
      this._props.session_id = UUID();
      cookie.set('session_id', this._props.session_id, THIRTY_MINUTES_AS_DAYS);
    }

    this._props.has_transacted = Boolean(this._props.has_transacted);
    this._props.hit_id = UUID();
  },
  transacted() {
    cookie.set('has_transacted', true, THIRTY_MINUTES_AS_DAYS);
  }
};

export default cookies;