import Tracker from "../../Tracker";
import { ELEMENT_TRACKING } from "../trackLabels";

export const checkbox = new Tracker(ELEMENT_TRACKING, 'checkbox', {
  data(element, event) {
    return {
      event_action: event.target.checked ? 'checked' : 'unchecked',
      event_label: element.nextElementSibling.textContent.trim()
    }
  }
});