import Tracker from "../../Tracker";
import { VIDEO_TRACKING } from "../trackLabels";
import UUID from '@nn/psdl-utility/src/uuid.js';
import scriptLoader from "../../../../scriptLoader/scriptLoader";

export const youtube = new Tracker(VIDEO_TRACKING, 'youtube', { addEventListener: false });

const youtubeApiStatus = {
  ready: false,
  handlers: []
};

const status = {
  'st_0': 'watched-to-end',
  'st_1': 'start',
  'st_2': 'pause'
};

function onYoutubeReady(cb) {
  if (youtubeApiStatus.ready) {
    cb();
  }

  youtubeApiStatus.handlers.push(cb);
  scriptLoader('https://www.youtube.com/iframe_api', true);
  window.onYouTubeIframeAPIReady = () => {
    youtubeApiStatus.ready = true;
    youtubeApiStatus.handlers.forEach(cb => cb());
    youtubeApiStatus.handlers = [];
  }
}

youtube.onElementAttached((element) => {
  onYoutubeReady(() => {

    let videoId;

    if (element.attributes.id !== undefined) {
      videoId = element.attributes.id.value;
    } else {
      videoId = 'youTubeVideo-' + UUID();
      element.setAttribute("id", videoId);
    }

    new window.YT.Player(videoId, {
      events: {
        'onStateChange': function (event) {
          if (status['st_' + event.data]) {
            youtube.publish({
              event_action: status['st_' + event.data],
              event_label: event.target.a.src
            });
          }
        }
      }
    })
  })
});

