import { isEmpty } from "@nn/psdl-utility/src/object-utils";

export function validateRequiredProperty(property, propertyName) {
  if (!property) {
    throw new TypeError('Missing required property [' + propertyName + ']');
  }
}

export function validateRequiredStringProperty(property, propertyName) {
  validateRequiredProperty(property, propertyName);
  if (typeof property !== 'string') {
    throw new TypeError('Constructor property [' + propertyName + '] is not of type string');
  }
}

export function validateRequiredNonEmptyProperty(property, propertyName) {
  validateRequiredProperty(property, propertyName);
  if (isEmpty(property)) {
    throw new TypeError('Empty object [' + propertyName + '] not allowed');
  }
}
