import UUID from "@nn/psdl-utility/src/uuid";
import { log } from "@nn/psdl-utility/src/console";
import relay42 from '../../relay42';

class Publisher {
  constructor() {
    this.queue = [];
    this.promise = null;
    this.flushDelayMilliseconds = 100;
  }

  publish(eventTracking, eventCategory, eventData) {
    if (this.isDuplicateInQue({ eventTracking, eventCategory, eventData })) {
      return log('[Duplicate Ignored] publishEvent', eventTracking, eventCategory, eventData);
    }

    this.queue.push({ eventTracking, eventCategory, eventData });
    if (!this.promise) {
      this.promise = new Promise((resolve) => {
        setTimeout(() => {
          this.flushQueue();
          resolve();
        }, this.flushDelayMilliseconds);
      })
    }
    return this.promise;
  }

  isDuplicateInQue(event) {
    for (let i = 0; i < this.queue.length; i++) {
      if (JSON.stringify(this.queue[i]) === JSON.stringify(event)) {
        return true;
      }
    }

    return false;
  }

  flushQueue() {
    this.queue.forEach(({ eventTracking, eventCategory, eventData }) => {
      eventData.hit_id = UUID();
      relay42.addLocalTagProperties({
        event_action: null,
        event_label: null,
        ...eventData
      }, {silent: true});
      relay42.publishEvent("dataLayerUpdate", "update", eventData, {silent: true});
      relay42.publishEvent(eventTracking, eventCategory, eventData);
    });
    this.queue = [];
    this.promise = null;
  }
}

export default new Publisher();