import Tracker from '../../Tracker.js';
import { NAVIGATION } from "../trackLabels";

export const mainMenu = new Tracker(NAVIGATION, 'main-menu');

export const mainMenuDropdowns = new Tracker(NAVIGATION, 'main-menu', {
  data(element, event) {
    let state = 'close';
    let event_label = event.target.textContent;
    if (element.parentNode.classList.contains('c-main-navigation__expanded')) {
      state = 'open';
    }
    if (element.parentNode.classList.contains('c-main-navigation__profile')) {
      event_label = 'mijn.nn menu';
    }
    return {
      event_action: state,
      event_label: event_label
    }
  }
}
);