import Tracker from '../../Tracker';
import analytics from '../../analytics';
import { ELEMENT_TRACKING } from '../trackLabels';

export const nba = new Tracker(ELEMENT_TRACKING, 'nba', {
  label(element, event) {
    return event.target.getAttribute('data-nba-code');
  },
})

nba.onElementFound((elements) => {
  const nbaCodes = analytics.getData().nn_nbacode ? analytics.getData().nn_nbacode.split(',') : [];
  elements.forEach(element => {
    const elementCode = element.getAttribute('data-nba-code');
    if(!nbaCodes.includes(elementCode)){
      nbaCodes.push(elementCode);
    }
  });

  analytics.prop({
    'nn_nbacode': nbaCodes.join(','),
    'nn_nba_counter': nbaCodes.length,
  });
});

nba.onElementRemoved((elements) => {
  const nbaCodes = analytics.getData().nn_nbacode ? analytics.getData().nn_nbacode.split(',') : [];

  const codesToRemove = elements.map(element => {
    return element.getAttribute('data-nba-code');
  });

  const filteredNbaCodes = nbaCodes.filter(code => {
    return !codesToRemove.includes(code)
  })

  analytics.prop({
    'nn_nbacode': filteredNbaCodes.join(','),
    'nn_nba_counter': filteredNbaCodes.length,
  });
});