import {
  validateRequiredNonEmptyProperty,
  validateRequiredStringProperty
} from "./utils/validators";
import { disableLogging, enableLogging } from "@nn/psdl-utility/src/console";
import documentState from "@nn/psdl-utility/src/documentState";
import publisher from './publisher.js'
import relay42 from "../../relay42";
import tagProperties from "./data/tagProperties";
import pagestructure from "./data/pagestructure";
import UUID from "@nn/psdl-utility/src/uuid";

class Analytics {
  constructor() {
    this.trackers = [];
    this.dataChanghedHandlers = [];
  }

  load() {
    relay42.setEnvironment(tagProperties.data.platform_environment);
    relay42.setStructurePrefix(pagestructure.getPrefix());

    this.scan();
    this.loadPage();
    this.startObserving();
  }

  loadPage() {
    relay42.setPageStructure(pagestructure.get());
    relay42.addLocalTagProperties(tagProperties.data);
    relay42.loadTags();
  }

  loadVirtualPage(virtualPage) {
    const { isRoot, skipLoad } = this.analyzeVirtualPage(virtualPage);
    if (skipLoad) {
      return;
    }

    this.previousVirtualPage = virtualPage;
    const previousPageProperties = this.getData();
    tagProperties.reload();
    relay42.resetTags();
    relay42.setPageStructure(pagestructure.get(virtualPage));
    relay42.addLocalTagProperties({
      ...previousPageProperties,
      ...tagProperties.data,
      virtualpage: !isRoot
    });
    relay42.loadTags();
  }

  analyzeVirtualPage(virtualPage) {
    const isRoot = virtualPage === '/';
    const firstLoad = !this.previousVirtualPage;
    const isNew = this.previousVirtualPage !== virtualPage;

    const skipLoad = (!virtualPage || !isNew || (firstLoad && isRoot));

    return { isRoot, skipLoad };
  }

  startObserving() {
    this.scanObserver = new MutationObserver((mutations) => {
      mutations.forEach(mutation => {
        Array.prototype.slice.call(mutation.addedNodes).forEach(node => {
          this.scan(node);
        });
        Array.prototype.slice.call(mutation.removedNodes).forEach(node => {
          this.scanRemovedNode(node);
        });
      })
    });

    documentState.onDOMContentLoaded(() => {
      this.scan();
      this.scanObserver.observe(document.body, { childList: true, subtree: true });
    })
  }

  scan(root) {
    root = root || document;
    this.trackers.forEach(tracker => tracker.scan(root));
  }

  scanRemovedNode(root) {
    this.trackers.forEach(tracker => tracker.scanRemovedNode ? tracker.scanRemovedNode(root) : null);
  }

  registerTracker(tracker) {
    if (this.trackers.indexOf(tracker) !== -1) {
      return;
    }

    if (!tracker.scan || typeof tracker.scan !== 'function') {
      throw Error('[Analytics] Aborting tracker registration as the required function \'scan\' is either missing or not a function.');
    }

    if (documentState.isDOMContentLoaded()) {
      tracker.scan();
    }
    this.trackers.push(tracker);
  }

  publish(eventTracking, eventCategory, eventData) {
    validateRequiredStringProperty(eventTracking, 'eventTracking');
    validateRequiredStringProperty(eventCategory, 'eventCategory');
    validateRequiredNonEmptyProperty(eventData, 'eventData');

    return publisher.publish(eventTracking, eventCategory, eventData)
  }

  setConsoleLogs(value) {
    if (value) {
      enableLogging();
      return "Analytics logging is turned on, refresh the page to see the logs appear";
    }
    disableLogging();
    return "Analytics logging is turned off";
  }

  prop(keyOrProperties, value) {
    let property = {};

    if (typeof keyOrProperties === 'string') {
      property[keyOrProperties] = value;
    } else {
      property = keyOrProperties;
    }

    const propStatus = {
      stop: false
    };

    this.dataChanghedHandlers.forEach(handler => {
      handler(property, propStatus);
    });

    if (!propStatus.stop) {
      this.addLocalTagProperties(property)
    }
  }

  addLocalTagProperties(property) {
    relay42.addLocalTagProperties(property);
    relay42.publishEvent("dataLayerUpdate", "update", property, {silent: true});
  }

  onDataChange(handler) {
    this.dataChanghedHandlers.push(handler);
  }

  getData() {
    return relay42.getLocalTagProperties();
  }

  transacted() {
    // Not sure if this is ever used
    tagProperties.transacted();
    this.prop({
      'has_transacted': true,
      'transaction_id': UUID(),
      transaction_revenue_month: null,
      transaction_revenue_year: null,
      transaction_tax_month: null,
      transaction_tax_year: null
    });
  }
}

export default new Analytics();
