import Tracker from "../../Tracker";
import { ELEMENT_TRACKING } from "../trackLabels";

export const lightbox = new Tracker(ELEMENT_TRACKING, 'lightbox', { addEventListener: false });

function publishLightbox(element, action) {
  const header = element.querySelector('h1,h2,h3,h4,.h1,.h2,.h3,.h4, [data-role="sidepanel-title"]');
  lightbox.publish({
    event_action: action,
    event_label: header ? header.textContent : null
  })
}

lightbox.onElementFound(elements => {
  elements.forEach(element => {
    publishLightbox(element, 'click');
  })
});

lightbox.onElementRemoved(elements => {
  elements.forEach(element => {
    publishLightbox(element, 'close');
  })
});