import {
  validateRequiredNonEmptyProperty,
  validateRequiredProperty,
  validateRequiredStringProperty
} from "./utils/validators";
import publisher from "./publisher";

const defaultEvent = {
  type: 'click',
  addEventListener: true,
  action() {
    return 'click';
  },
  label(element) {
    return element.textContent
  },
  data() {
    return {}
  },
};

export default class Tracker {
  constructor(eventTracking, eventCategory, event) {
    validateRequiredStringProperty(eventTracking, 'eventTracking');
    validateRequiredStringProperty(eventCategory, 'eventCategory');

    this.elementAttachedHandlers = [];
    this.elementFoundHandlers = [];
    this.elementRemovedHandlers = [];
    this.eventTracking = eventTracking;
    this.eventCategory = eventCategory;
    this.event = {
      ...defaultEvent,
      ...event
    };

    this.selectors = [];
    this.skippedSelectors = [];

    this.name = this.eventTracking + '|' + this.eventCategory;
  }

  publish(eventData) {
    validateRequiredProperty(eventData, 'eventData');
    validateRequiredNonEmptyProperty(eventData, 'eventData');

    publisher.publish(this.eventTracking, this.eventCategory, eventData);
  }

  alreadyAttached(element) {
    element.trackers = element.trackers || [];
    return element.trackers.indexOf(this.name) !== -1;
  }

  onElementAttached(handler) {
    this.elementAttachedHandlers.push(handler);
    return this;
  }

  onElementFound(handler) {
    this.elementFoundHandlers.push(handler);
    return this;
  }

  onElementRemoved(handler) {
    this.elementRemovedHandlers.push(handler);
    return this;
  }

  attachElement(element) {
    if (this.alreadyAttached(element)) {
      return this;
    }

    if (this.event.addEventListener) {
      element.addEventListener(this.event.type, (event) => {
        this.publish({
          event_action: this.event.action(element, event),
          event_label: this.event.label(element, event),
          ...this.event.data(element, event)
        })
      });
    }

    element.trackers.push(this.name);
    this.elementAttachedHandlers.forEach(handler => handler(element));
    return this;
  }

  attachSelector(selector) {
    if (this.selectors.indexOf(selector) !== -1) {
      return this;
    }

    this.selectors.push(selector);
    return this;
  }

  skipSelector(selector) {
    if (this.skippedSelectors.indexOf(selector) !== -1) {
      return this;
    }

    this.skippedSelectors.push(selector);
    return this;
  }

  getMatchedElements(root) {
    const selector = this.selectors.join(',');
    const elements = [...root.querySelectorAll(selector)];
    if (root.matches && root.matches(selector)) {
      elements.push(root);
    }

    if (elements.length > 0 && this.skippedSelectors.length > 0) {
      const skipSelector = this.skippedSelectors.join(',');
      return elements.filter(e => !(e.matches && e.matches(skipSelector)));
    }

    return elements
  }

  scan(root) {
    root = root || document;
    if (!root.querySelectorAll || this.selectors.length === 0) {
      return;
    }

    const elements = this.getMatchedElements(root);
    if (elements.length > 0) {
      elements.forEach(this.attachElement.bind(this));
      this.elementFoundHandlers.forEach(handler => handler(elements));
    }
  }

  findAllAttached(root) {
    const found = [];
    if (this.alreadyAttached(root)) {
      found.push(root);
    }

    return [
      ...found,
      ...this.findInChildNodes(root.childNodes)
    ];
  }

  findInChildNodes(childNodes) {
    return [...childNodes].reduce((found, node) => {
      if (this.alreadyAttached(node)) {
        found.push(node);
      }
      return [
        ...found,
        ...this.findInChildNodes(node.childNodes)
      ]
    }, []);
  }

  scanRemovedNode(removedNode) {
    if (!removedNode || !(removedNode instanceof Element) || this.elementRemovedHandlers.length === 0) {
      return;
    }

    const elements = this.findAllAttached(removedNode);
    if (elements.length > 0) {
      this.elementRemovedHandlers.forEach(handler => handler(elements));
    }
  }
}