import Tracker from "../../Tracker";
import { ELEMENT_TRACKING } from "../trackLabels";

export const toggle = new Tracker(ELEMENT_TRACKING, 'switch', {
  data(element, event) {
    let state = event.target.checked ? 'on' : 'off';
    let label = element.nextElementSibling.classList.contains('c-form-label') ? element.nextElementSibling.textContent.trim() : '';
    return {
      event_action: state,
      event_label: label
    }
  }
});