let queryStringVariables = {};

function parseQueryString(locationString) {
  if (locationString.indexOf('?') !== -1) {
    const query = locationString
      .substring(1)
      .replace(/^.*?\?/, '')
      // and remove any existing hash string (thanks, @vrijdenker)
      .replace(/#.*$/, '')
      .split('&');

    query.forEach((keyValuePair) => {
      const [key, value] = keyValuePair.split('=');
      queryStringVariables[decodeURIComponent(key)] = decodeURIComponent(value);
    });
  }

  return queryStringVariables;
}

function queryString(key) {
  if (queryStringVariables[key]) {
    return queryStringVariables[key];
  }

  return null;
}

function queryStringAll() {
  return queryStringVariables;
}

function buildQueryString(data) {
  const pairs = [];
  for (let key in data) {
    if (data.hasOwnProperty(key) && data[key]) {
      if (Array.isArray(data[key])) {
        data[key].forEach(item => {
          pairs.push(encodeURIComponent(key + '[]') + '=' + encodeURIComponent(item))
        });
      } else {
        pairs.push(encodeURIComponent(key) + '=' + encodeURIComponent(data[key]));
      }
    }
  }

  return pairs.join('&');
}

window.app = window.app || {};
window.app.queryString = (function () {
  parseQueryString(window.location.search);
  return queryString;
})();


export { buildQueryString, parseQueryString, queryString, queryStringAll }
