import Tracker from '../../Tracker';
import { ERROR_TRACKING } from '../trackLabels';

export const error = new Tracker(ERROR_TRACKING, 'error', { addEventListener: false });

error.scan = function() {
  if(!error.initialized) {
    error.initialized = true;
    if (document.querySelector('link[rel="canonical"][href*="404.htm"]')) {
      error.publish({
        event_action: 404,
        event_label: document.referrer
      });
    }

    if ($) {
      $(document).ajaxError((event, jqXHR) => {
        error.publish({
          event_action: jqXHR.status,
          event_label: window.location.href,
          'jquery': 'YES'
        });
      });
    }
  }
};
