import meta from './meta.js';

export default {
  normalizedPathParts(path) {
    if (!path || typeof path !== "string") {
      return [];
    }

    return path.split('/')
      .filter(function (value) {
        return "" !== value && null !== value && undefined !== value;
      })
      .map(function (value) {
        return value
          .replace('.html', '')
          .replace('.htm', '');
      });
  },
  getBase() {
    const pageStructProps = [
      meta.props.page_audience,
      meta.props.page_category,
      meta.props.page_product,
      meta.props.page_type,
    ].filter(function (value) {
      return !!value;
    });

    if (pageStructProps.length === 4) {
      return pageStructProps.join('|');
    }

    if (meta.tags.pagestructure) {
      return meta.tags.pagestructure;
    }

    return this.normalizedPathParts(document.location.pathname).join('|');
  },
  get(virtualPage) {
    const virtualPath = this.normalizedPathParts(virtualPage).join('/');
    return this.getBase() + (virtualPath ? '|' + virtualPath : '');
  },
  getPrefix() {
    return [meta.tags.portal, meta.tags.env].join('|')
  }
}