import Tracker from "../../Tracker";
import { ELEMENT_TRACKING } from "../trackLabels";
import analytics from '../../analytics';

export const breadcrumb = new Tracker(ELEMENT_TRACKING, 'breadcrumb');

export const breadcrumbProps = new Tracker(ELEMENT_TRACKING, 'breadcrumbProps', { addEventListener: false });

breadcrumbProps.onElementFound((elements) => {
  analytics.prop({
    'page_breadcrumbs': createPageBreadcrumbList(elements),
  });
});

breadcrumbProps.onElementRemoved(() => {
  const elements = [...document.querySelectorAll(breadcrumbProps.selectors.join(','))];
  const page_breadcrumbs = createPageBreadcrumbList(elements);

  if(analytics.getData().page_breadcrumbs && Object.values(analytics.getData().page_breadcrumbs).toString() !== page_breadcrumbs.toString()){
    analytics.prop({
      'page_breadcrumbs': page_breadcrumbs,
    });
  }
});

const createPageBreadcrumbList = (elements) => {
  let currentBreadcrumbList = elements;
  if(elements.length> 0){
    currentBreadcrumbList = [...elements.pop().parentElement.children].map(el=> {
      return el.innerText;
    });
  }

  return currentBreadcrumbList;
}
